import './Home.css'
import { useState } from 'react'

function EvalFunction( ){
const [inputValue, setInputValue] = useState("")

const valueChange = (event) => {
    setInputValue(event.target.value)
  }

function equals(){
    try{
   setInputValue (eval(inputValue));
    }
    catch(error){
        setInputValue('Error')
    }
}
function OnClickFunction (value){
    setInputValue((prevInput) => prevInput + value);
  };

//   function OnKeyDownFunction (event) {
//     const key = event.key;
//     if (/ [0-9+\-*/=]/.test(key)) {
//       OnClickFunction(key);
//     } else if (key === 'Enter') {
//       equals();
//     } else if (key === 'Escape') {
//       clear();
//     }
//   };

function OnKeyDownFunction(event){
    const key = event.key;
    if (key === '+' || key ==='-' ){
        OnClickFunction(key)

    }
    else if (key === 'Enter'){
        equals();
    }
    else if(key === 'Escape' || key === 'C' || key === 'c'){
        clear();
    }
}

function clear( ){
    setInputValue('')
}
function back( ){
    let str;
    str= inputValue;
    str = str.substring(0, str.length - 1);
    setInputValue(str)
}
  return (
    <div>
        <div className='wrapper'>
            <div className='container'>
                <div className='row1'>
                    <input type='number' className='inputText' id='text' value={inputValue} onChange={valueChange} />
                </div>
                <div className='row2'>
                    <button className='b1' onClick={clear} onkeydown="myFunction(this)">C</button>
                    <button className='b1' >% </button>
                    <button className='b1' onClick={back}>Back</button>
                    <button className='b1'>/</button>
                </div>
                <div className='row3'>
                    <button className='b2' >7</button>
                    <button className='b2' >8</button>
                    <button className='b2' >9</button>
                    <button className='b1' >*</button>
                </div>
                <div className='row4'>
                    <button className='b2' >4</button>
                    <button className='b2'>5</button>
                    <button className='b2' >6</button>
                    <button className='b1' >-</button>
                </div>
                <div className='row5'>
                    <button className='b2'>1</button>
                    <button className='b2'>2</button>
                    <button className='b2'>3</button>
                    <button className='b1'>+</button>
                </div>
                <div className='row6'>
                    <button className='b2'>00</button>
                    <button className='b2' >0</button>
                    <button className='b2'>.</button>
                    <button className='b3' onClick={equals}>=</button>
                </div>
            </div>
        </div>
    </div>
)
}

export default EvalFunction;